<template>
  <v-card style="width: 100%; border: none;" outlined>
    <v-data-table
      v-model="selected"
      :headers="headers"
      :items="desserts"
      :server-items-length="totalDesserts"
      :options.sync="options"
      @dblclick:row="dblclickrow"
      disable-sort
      show-select
      class="elevation-1"
      :footer-props="footerProps"
    >
      <template v-slot:item.username = "{ item }">
        <div style="white-space: nowrap;">{{item.username}}</div>
      </template>
      <template v-slot:item.fullname = "{ item }">
        <div style="white-space: nowrap;">{{item.fullname}}</div>
      </template>
      <template v-slot:item.departmentPosition = "{ item }">
        <div v-html="item.departmentPosition"></div>
      </template>
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>{{$t('userList')}}</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <Create-User-Btn 
            :departments="departments"
            :deptArray="deptArray"
            @loadUsers="loadUsers"
          />

          <Delete-User-Btn
            class="ml-4"
            :selected="selected"
            @loadUsers="loadUsers"
          />

          <v-spacer></v-spacer>
          <v-text-field
            @keyup.enter="loadUsers(true)"
            v-model="keywords"
            :label="$t('fullNameOrusername')"
            single-line
            hide-details
            style="max-width: 160px;"
          >
            <template v-slot:append>
              <v-icon @click="loadUsers(true)">mdi-magnify</v-icon>
            </template>
          </v-text-field>
        </v-toolbar>
      </template>
      <!-- <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length">
          <v-row>
            <v-col>性别:</v-col>
            <v-col>{{ item.sex }}</v-col>
            <v-col>联系电话:</v-col>
            <v-col>{{ item.phone }}</v-col>
          </v-row>
          <v-row>
            <v-col>邮箱:</v-col>
            <v-col>{{ item.email }}</v-col>
            <v-col>地址:</v-col>
            <v-col>{{ item.address }}</v-col>
          </v-row>
        </td>
      </template> -->
      <template v-slot:item.actions="{ item }">
        <div style="white-space: nowrap;">
          <Edit-User-Btn
            class="mr-2"
            :departments="departments"
            :item="item"
            @loadUsers="loadUsers"
          />

          <Delete-User-Btn
            class="mr-2"
            :item="item"
            @loadUsers="loadUsers"
          />
        </div>
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    selected: [],
    keywords: '',
    totalDesserts: 0,
    options: {},
    headers: [
      {
        text: "用户名",
        align: "start",
        value: "username",
      },
      { text: "姓名", value: "fullname" },
      { text: "角色", value: "roleNames" },
      { text: "部门/岗位", value: "departmentPosition" },
      { text: "操作", value: "actions", align: "end" }
    ],
    desserts: [],
    deptArray: [],
    selections: [],

    footerProps: {
        showFirstLastPage: true,
        firstIcon: 'mdi-arrow-collapse-left',
        lastIcon: 'mdi-arrow-collapse-right',
        
        disablePagination: false,
        itemsPerPageOptions: [5, 10,20],
      },
  }),

  props: {
    departments: {
      type: Array
    }
  },

  computed: {
    
  },

  watch: {
    options: {
      handler() {
        this.loadUsers();
      },
      deep: true
    }
  },

  created() {
    
  },

  mounted() {
    this.$emit(
          "selectUserReady"
        );
  },

  methods: {
    //双击事件demo
    dblclickrow(event, obj) {
      console.log(event);
      console.log(obj);
    },

    loadUsers(notClearKeywords) {
      if(!notClearKeywords) {
        this.keywords = "";
      }

      if (this.deptArray.length != 0) {
        const deptUniqueCode = this.deptArray[0].uniqueCode;
        this.$http
          .post("/user/api/v1/user/pageMap", {
            departmentUniqueCode: deptUniqueCode,
            pageNum: this.options.page,
            pageSize: this.options.itemsPerPage,
            keywords: this.keywords
          })
          .then(res => {
            if (res.data.records) {
              this.totalDesserts = res.data.total;
              this.desserts = res.data.records;
            } else {
              this.totalDesserts = 0;
              this.desserts = [];
            }
          })
          .catch(error => {
            this.$store.commit("SET_SNACK", {
              success: false,
              msg: "访问异常：未获取到用户"
            });
          });
      } else {
        this.desserts = [];
      }
    },
  },

  components: {
    CreateUserBtn: () => import("./CreateUser"),
    EditUserBtn: () => import("./EditUser"),
    DeleteUserBtn: () => import("./DeleteUser"),
  }
};
</script>
